import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import * as serviceWorker from "./serviceWorker";
import store from "./redux/store.js";

import HomePage from "./_web/home-page/HomePage";
import YourFundsPage from "./_web/your-funds-page/YourFundsPage";
import FundDetailsPage from "./_web/fund-details-page/FundDetailsPage";
import AddNewFundPage from "./_web/add-new-fund-page/AddNewFundPage";
import VaultsPage from "./_web/vaults-page/VaultsPage";
// web3 connect
import Web3 from "web3";
import { Web3ReactProvider } from "@web3-react/core";
import ProtectedRoute from "./_web/ProtectedRoute/ProtectedRoute";
import LoaderOverlary from "./_web/LoaderOverlay/LoaderOverlay";

//TOASTR
import ReduxToastr from "react-redux-toastr";

//CSS
import "./onboardStyling.css";
import "react-redux-toastr/lib/css/react-redux-toastr.min.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "./index.css";
import  ManageVaultPage  from "./_web/manage-vaults-page/ManageVaultPage";
import Layout from "./_web/global/layout/Layout";
import Profile from "./_web/components/Profile/Profile";
//get Library
function getLibrary(provider) {
  return new Web3(provider);
}

const app = (
  <Web3ReactProvider getLibrary={getLibrary}>
    <React.StrictMode>
      <Provider store={store}>
        <LoaderOverlary>
          <BrowserRouter>
            <Layout>
              <Switch>
                <Route path="/" exact component={HomePage} />
                <Route path="/your-funds" exact component={YourFundsPage} />
                <Route path="/fund/:address" exact component={FundDetailsPage} />
                <ProtectedRoute
                  path="/add-new-fund"
                  exact
                  component={AddNewFundPage}
                />
                <ProtectedRoute
                  path="/manage"
                  exact
                  component={ManageVaultPage}
                />
                <ProtectedRoute
                  path="/profile"
                  exact
                  component={Profile}
                />
                <Route path="/vaults" exact component={VaultsPage} />
              </Switch>
            </Layout>
          </BrowserRouter>
        </LoaderOverlary>
        <ReduxToastr
          timeOut={4000}
          newestOnTop={false}
          preventDuplicates
          position="top-right"
          getState={(state) => state.toastr} // This is the default
          transitionIn="fadeIn"
          transitionOut="fadeOut"
          progressBar
          closeOnToastrClick
        />
      </Provider>
    </React.StrictMode>
  </Web3ReactProvider>
);
ReactDOM.render(app, document.getElementById("root"));
serviceWorker.unregister();
